import { useRouteMatch } from "react-router-dom";
import { useStores } from "../store";
import { useCallback } from "react";
import { BlockerModel } from "../models";

const useRouteBlocker = () => {
  const {
    params
  } = useRouteMatch();
  const {
    cookieStore
  } = useStores(); // Get blocker or return draft object

  const blockerId = +params.blocker;
  const id = isNaN(+blockerId) ? 0 : +blockerId;
  const queried = !!blockerId;
  const blocker = cookieStore.blockers.entries.get(id) || new BlockerModel(cookieStore.blockers, {
    id: 0
  });
  const link = "#/blocker";
  const editLink = useCallback(({
    key
  }) => "#/blocker/edit/".concat(key), [blocker]);
  const addLink = "#/blocker/new";
  return {
    blocker,
    id,
    queried,
    fetched: blocker.key !== 0,
    link,
    editLink,
    addLink
  };
};

export { useRouteBlocker };